$tile-background: radial-gradient(
  50% 50% at 50% 50%,
  #004465 40.62%,
  #003d5a 100%
);
$border-gradient: linear-gradient(
  135.94deg,
  #5dc4d9 1.59%,
  rgba(93, 196, 217, 0.4) 50.28%,
  #5dc4d9 100%
);
$opening-animation-duration: 0.4s;

.root {
  width: 100%;
  height: 100%;
  background: $tile-background;
  padding: 3px;
  border-radius: 6px;
  max-height: 108px;
  max-width: 108px;
}

.loading {
  pointer-events: none;
}

.loader {
  width: 100%;
  height: 100%;
}

@keyframes openTile {
  from {
    transform: scale(1.1);
  }
  50% {
    transform: scale(1);
  }
  to {
    transform: scale(1);
  }
}

.openTile {
  animation-name: openTile;
  animation-duration: $opening-animation-duration;
}

.hide {
  transition: transform 0.1s;
  &:hover {
    transform: scale(1.05);
    cursor: pointer;
  }
}

.highlightTile {
  background: $border-gradient !important;
}

.overflowHidden {
  overflow: hidden;
}

.tile {
  position: relative;
  width: 100%;
  height: 100%;
  background: $tile-background;
  text-align: center;
  overflow: visible;
  border-radius: 6px;
}

.bomb {
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 100%;
  bottom: 3%;
  left: 4%;
  transform: scale(1.55);
  user-select: none;
}

.hiddenBomb {
  height: 100%;
  transform: scale(1);
  width: 75%;
  user-select: none;
}

.diamond {
  width: 100%;
  height: 100%;
  transform: scale(0.8);
  filter: drop-shadow(0px 5.31447px 30px rgba(171, 205, 255, 0.2))
    drop-shadow(0px 0px 30px rgba(142, 181, 239, 0.3));
  user-select: none;
}

.openHidden {
  filter: brightness(0.2);
  padding: 0;
  & img {
    filter: none;
  }
}

.hiddenDiamond {
  transform: scale(0.65);
}

.open {
  background: black;
  animation-name: open;
  animation-duration: $opening-animation-duration;
  text-align: center;
}

@keyframes open {
  from {
    transform: scale(0);
  }
  to {
    transform: scale(1);
  }
}

@keyframes explode {
  from {
    background-position: center 3504px;
  }
  to {
    background-position: center -50px;
    background-image: none;
  }
}

.explode {
  background-image: url(/assets/animations/explosion.png);
  animation: explode 800ms steps(14) forwards;
}

.selected {
  background: $border-gradient;
}
