@import "sass/index";
@import "../../variables.module.scss";
$desktop-gap: 10px;
$mobile-gap: 5px;
.root {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 301px;
  background: $default;
  @include backgroundOptions;
}

.field {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: repeat(5, 1fr);
  gap: $desktop-gap;
  width: 100%;
  height: 100%;
  max-width: 287px;
  max-height: 283px;
}
