$inputHeight: 29;
$inputPaddingX: 5;
.root {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: end;
  width: 100%;
}
.title {
  margin: 0;
  padding: 5px 0;
  font-family: "Gilroy";
  font-style: normal;
  font-weight: 700;
  font-size: 6.81919px;
  line-height: 8px;

  color: #ffffff;

  mix-blend-mode: normal;
  opacity: 0.8;
}
.input {
  background: #0e1f21;
  border: 0.487085px solid #0d5057 !important;
  border-radius: 3.89668px;
  height: #{$inputHeight}px;
  width: ca1c(100% - #{inputPaddingX}px);

  font-family: "Gilroy";
  font-weight: 400;
  font-size: 9px;
  line-height: 10px;
  color: #ffffff;
  padding: 0 #{$inputPaddingX}px;

  display: flex;
  align-items: center;
}
.adronmentWrapper {
  height: #{$inputHeight}px;
  width: fit-content;
  display: flex;
  justify-content: center;
  align-items: center;

  color: white;
  position: absolute;
  right: #{$inputPaddingX}px;
  bottom: 0;
}
