@import "sass/index";
@import "../../variables.module.scss";
.root {
  position: relative;
  padding: 9px;
  background: $default;
  @include backgroundOptions;
  display: flex;
  flex-direction: column;
  height: 306px;
  justify-content: space-between;
  @media (max-width: 600px) {
    padding: 5px;
  }
}
.setCardRoot {
  background: #242234;
  opacity: 0.8;
  box-shadow: 0px 11.69px 14.6125px rgba(0, 0, 0, 0.19758);
  border-radius: 3.89668px;
  padding: 4px;
  gap: 3px;
  & > span {
    display: flex;
    align-items: center;
    line-height: 12px;
    padding-top: 0;
    padding-bottom: 0;
  }
  & > span:nth-child(3n + 1) {
    font-weight: 700;
    font-size: 7.79336px;
    padding-left: 10px;
    text-transform: uppercase;
    background-color: black;
  }

  & > span:nth-child(3n + 2),
  span:nth-child(3n + 3) {
    font-weight: 700;
    font-size: 8.76753px;
    line-height: 11px;
    justify-content: center;

    img {
      width: 12px;
      height: auto;
    }
  }
}
.field {
  width: 100%;
  height: fit-content;

  display: flex;
  justify-content: center;
  gap: 14px;
}

.card {
  position: relative;
  width: 58px;
  height: 82px;
  & > div:nth-child(2) {
    transform: scale(0.5) translate(-31px, -36px);
  }
  & > div:nth-child(3) {
    transform: scale(0.5) translate(-11px, -102px);
  }
  & > div:last-child {
    transform: scale(0.5) translate(29px, -158px);
  }
  & > span {
    left: -6px;
    top: -27px;
    height: fit-content;
  }

  @media (max-width: 400px) {
    width: 44px;
    height: 70px;
    & > div:nth-child(2) {
      transform: scale(0.4) translate(-31px, -48px);
    }
    & > div:nth-child(3) {
      transform: scale(0.4) translate(-19px, -146px);
    }
    & > div:last-child {
      transform: scale(0.4) translate(30px, -224px);
    }
    & > span {
      left: -6px;
      top: -24px;
      padding: 2px 12px;
      font-size: 9px;
      line-height: 12px;
    }
  }
}
