.root {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 4px;
  padding: 10px;
  border: 1px solid #4e4e9a;

  display: flex;
  justify-content: space-between;
  align-items: center;
}
.gameInfo {
  display: flex;
  gap: 14px;
  align-items: center;
}
.gameImg {
  height: 54px;

  img {
    width: auto;
    height: 100%;
  }
}
.info {
  display: grid;
  gap: 15px;
  color: #ffffff;
  font-family: "Gilroy";

  h4 {
    margin: 0;
    padding: 0;
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    text-transform: uppercase;
  }
  span {
    font-weight: 700;
    font-size: 12px;
    line-height: 15px;
  }
}

.playButton {
  background: #4734e9;
  border-radius: 10px;
  min-height: 42px;
  padding: 0px 44px;

  font-family: "Gilroy";
  font-weight: 700;
  font-size: 20px;
  line-height: 30px;

  display: flex;
  align-items: center;
  text-align: center;
  text-transform: uppercase;

  color: #ffffff;
  @media (max-width: 420px) {
    padding: 0px 10px;
    font-size: 14px;
  }
}
