@import "sass/index";

$titleHeight: 21;
$titleMobileMargin: 30;

.paper {
  background: linear-gradient(130.53deg, #24375f 2.16%, #000000 60.27%);
  border-radius: 30px;
  width: 578px;
  padding: 30px 25px;
  gap: 20px;
  @media (max-width: $smallScreen) {
    padding: 10px;
    width: 100%;
    border-radius: 0;
  }
}
.box {
  max-height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.scrollContainer {
  height: 100%;
  overflow: auto;
  padding-right: 5px;
}
.titleWrapper {
  display: flex;
  text-transform: uppercase;
  align-items: center;
  font-weight: 700;
  font-size: 16px;
  line-height: 21px;
  height: 21px;

  & > div {
    padding-bottom: 0;
    padding-top: 8px;
  }

  @media (max-width: $smallScreen) {
    width: 100%;
    margin-bottom: #{$titleMobileMargin}px;

    position: relative;
    justify-content: space-between;
  }
}

.root {
  width: 100%;
  color: $m_text-block-color;
  padding: 10px;
  position: relative;
  &::-webkit-scrollbar {
    display: none;
  }

  @media (max-width: $mobileScreen) {
    max-height: 100%;
    max-width: 100%;
    height: 100%;
    margin: 0;
  }
}
.closeButton {
  position: absolute;
  top: 0;
  right: 0;
  padding: 0;
  z-index: 2;
}
.title {
  margin-right: 18px;
  padding: 0 5px;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
  background: #01e2f9;
  text-align: center;
  color: #4b4bf3;

  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
}
.mainTitle {
  margin-top: 10px;
}
.header {
  color: #ffffff;
  flex-grow: 1;

  @media (max-width: $mobileScreen) {
    position: absolute;
    top: #{$titleMobileMargin + $titleHeight}px;
  }
}

.data {
  display: flex;
  align-items: stretch;
  margin: 20px 0;
  justify-content: space-between;
  gap: 14px;
  width: 100%;
  @media (max-width: 370px) {
    gap: 5px;
  }
}
.cell {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 10px;
  width: 167px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 10px;

  gap: 10px;

  position: relative;
  @media (max-width: 370px) {
    padding: 5px;
    width: fit-content;
  }

  // &:first-child::after {
  //   content: "";
  //   position: absolute;
  //   top: 0;
  //   right: 0;
  //   width: 1px;
  //   height: 100%;
  //   background: $m_text-block-color;

  //   @media (max-width: 460px) {
  //     width: calc(100% - 20px);
  //     height: 1px;
  //     right: 10px;
  //     top: calc(100% + 10px);
  //   }
  // }
}
.text,
.amount {
  text-overflow: ellipsis;
  overflow: hidden;
  width: max-content;
  margin: 0;

  font-weight: 700;
  font-size: 10px;
  line-height: 12px;
  letter-spacing: 0.5px;

  color: #ffffff;

  opacity: 0.5;
}
.textWrapper {
  width: max-content;
  display: flex;
  align-items: center;
}
.coinIcon {
  position: static;
  margin-right: 3px;
  height: 18px;
  width: 18px;
  @media (max-width: 370px) {
    height: 15px;
    width: 15px;
  }
}
.amount {
  font-size: 11px;
  line-height: 13px;
  text-transform: uppercase;

  @media (max-width: 370px) {
    font-size: 8px;
    line-height: 10px;
  }
}
.textLink {
  width: 100%;
  max-width: 244px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  text-decoration: none;
  font-weight: 600;
  font-size: 13px;
  line-height: 16px;
  color: #ffffff;
  border-radius: 0;
  text-transform: uppercase;
  padding: 0px 62px;
  border: 1px solid #4734e9;
  background: transparent;
  box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.17783);
  &:hover {
    background: linear-gradient(
      180deg,
      rgba(255, 255, 255, 0.0620629) 0%,
      rgba(255, 255, 255, 0.0755026) 100%
    );
  }
  @media (max-width: 460px) {
    padding: 0px 25px;
  }
  & > * {
    transition: transform 1s;
  }
  & > *:hover {
    transform: scale(1.075);
  }
}
.lose {
  color: $lose-red;
}
.win {
  color: $win-green;
}

.input {
  height: 40px;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid #4e4e9a;
  border-radius: 4px;
  margin: 0;

  width: 100%;
  & > input {
    padding-right: 40px;
    font-size: 14px;
    line-height: 17px;
    color: #ffffff;
  }
}
.progressWrapper {
  width: 95%;
  height: 50%;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
}
.inpBox {
  display: flex;
  flex-direction: column;
  padding: 10px 0;
}
.boxItem {
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.clientContent {
  display: grid;
  justify-content: space-between;
  grid-template-rows: max-content;

  align-items: center;

  grid-template-columns: 1fr max-content;
  gap: 20px;
}
.nonceInput {
  width: 130px;
}
.simpleBox {
  display: flex;
  color: #ffffff;
  justify-content: center;
}
.dataBox {
  display: flex;
  flex-direction: row;
  margin: 10px auto;
  align-items: center;
}
.userData {
  margin-left: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  font-weight: 600;
  font-size: 13px;
  line-height: 16px;
  color: #ffffff;
}
.userName {
}
.userDate {
  font-size: 11px;
  line-height: 13px;
  opacity: 0.5;
}
.resultImg {
  margin-left: auto;

  display: flex;
  justify-content: center;
  align-items: center;

  height: 70px;
  width: 70px;

  border-radius: 50px;
  background-color: #111c2f;

  img {
    width: auto;
    height: 65%;
  }
}
.inputContainer {
  display: flex;
}
.inputWrapper {
  width: 100%;
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  position: relative;

  & > div {
    margin: 0 !important;
  }
  & > span {
    width: 120px;
  }
}
.icon {
  position: absolute;
  right: 10px;
  padding: 7px;
  box-sizing: border-box;
  border-radius: 50%;
  @media (max-width: 460px) {
    right: 0;
  }
}
.nonceIcon {
  right: 0;
}
.shareContainer {
  display: flex;
  justify-content: center;
}
.share {
  display: flex;
  gap: 10px;
  align-items: center;
  color: #ffffff;
  text-align: center;
  text-transform: uppercase;
  font-size: 15px;
  font-weight: bold;
  line-height: 18px;

  cursor: pointer;
}
.shareImg {
  width: 24px;
  height: 24px;
  padding: 0;
}
.shareImgWrapper {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(-100%, -50%);
  width: auto;
  height: auto;
  background: black;
}
.shareImg {
  width: 100%;
  height: 100%;
}
