@import "../../variables.module.scss";
.root {
  width: 100%;
  background: transparent;
  height: 286px;
  background: $default;
  @include backgroundOptions;
  display: flex;
  flex-direction: column;
  justify-content: end;
  padding-bottom: 25px;
}
.rowRoot {
  cursor: default;
  background: #2c2a45;
  opacity: 0.9;
  border-radius: 2.92251px;

  width: 100%;
  max-width: 195px;
  height: 20px;

  position: absolute;
  left: 14px;
  top: 14px;

  & > span {
    font-weight: 400;
    font-size: 10px;
    line-height: 12px;

    color: #ffffff;
  }
}
.combinations {
  svg {
    width: 10%;
  }
}
.fieldRoot {
  display: grid;
  grid-template-rows: max-content;
  grid-template-columns: repeat(5, 183px);
  justify-content: center;
  align-items: center;
  gap: 10px;
  opacity: 0;
}
.visible {
  opacity: 1;
}
