@import "sass/index";
@import "../../variables.module.scss";

.root {
  width: 100%;
  height: 287px;
  padding: 50px 8px;
  top: 0;
  left: 0;

  background: $limbo;
  background-position: bottom 50% center;
  background-repeat: no-repeat;
  background-size: cover;

  display: flex;
  justify-content: center;
  position: relative;
  @media (max-width: 600px) {
    height: 50vw;
  }
}

.footer {
  width: 100%;
  height: 118px;

  position: absolute;
  bottom: 0;
  z-index: 0;

  display: flex;
  align-items: center;
}
.inputs {
  width: 100%;
  padding: 0 35px;

  display: flex;
  justify-content: space-between;
  gap: 10px;

  & > div {
    max-width: 224px;
  }
  input {
    font-weight: 400;
    font-size: 9px;
    line-height: 10px;

    color: #ffffff;
  }
  & > div > div > div {
    background: #0e1f21;
    border: 0.487085px solid #0d5057 !important;
    border-radius: 3.89668px;
    height: 29px;
    margin-top: 13px;
    & > div {
      font-weight: 400;
      font-size: 12px;
      line-height: 14px;

      color: #ffffff;
    }
  }
  &.inputs label {
    font-family: "Gilroy";
    font-style: normal;
    font-weight: 700;
    font-size: 9px;
    line-height: 10px;

    color: #ffffff;

    mix-blend-mode: normal;
    opacity: 0.8;
  }
}
.labels {
  position: absolute;
  top: 9px;
  right: 15px;

  display: flex;
  align-items: center;
  gap: 3px;
}
.label {
  height: 16px;
  padding: 0px 9px;

  border-radius: 6.81919px;

  font-family: "Gilroy";
  font-weight: 700;
  font-size: 11.5565px;
  line-height: 12px;

  color: #150a30;

  display: flex;
  justify-content: center;
  align-items: center;
}

.red {
  background: #e25f5f;
}
.green {
  background: #b5eb8c;
}
